import { NgModule } from "@angular/core";
import {
  Routes,
  RouterModule,
  Route,
  PreloadAllModules,
} from "@angular/router";
import { BaseLayoutComponent } from "./Layout/base-layout/base-layout.component";
import { PagesLayoutComponent } from "./Layout/pages-layout/pages-layout.component";

// // Pages
const route: Route[] = [
  {
    path: "",
    component: PagesLayoutComponent,
    children: [
      // User Pages
      // {
      //   path: "login",
      //   component: LoginComponent,
      //   data: { extraParameter: "" },
      // },
      {
        path: "",
        canActivate: [],
        canActivateChild: [],
        component: PagesLayoutComponent,
        data: {
          layout: "empty",
        },
        children: [
          {
            path: "login",
            loadChildren: () =>
              import("./modules/auth/login/login.module").then(
                (m) => m.LoginModule
              ),
          },
        ],
      },
      {
        path: "",
        canActivate: [],
        canActivateChild: [],
        component: BaseLayoutComponent,
        data: {
          layout: "empty",
        },
        children: [
          {
            path: "siniestros",
            loadChildren: () =>
              import("./modules/siniestro/siniestro.module").then(
                (m) => m.SiniestroModule
              ),
          },
        ],
      },
    ],
  },
];

